body {
    margin: 0;
    padding: 0;
    max-width: 600px;
    margin-inline-start: auto;
    margin-inline-end: auto;
}

h1 {
    font-size: 36px;
}

.pages {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.link {
    text-decoration: none;
    color: darkgray;
    font-weight: 700;
    font-size: 24px;
}

.heading {
    margin: 0;
}

.song_cart {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: black;
    color: white;
    padding: 32px;
    margin-block: 16px;
}

.voting {
    display: flex;
    flex-direction: row;
    margin-block-start: 8px;
}

.up_votes_icon,
.down_votes_icon {
    margin-block-end: -8px;
}

.down_votes {
    margin-inline-start: 32px;
}

.styles {
    display: flex;
    flex-direction: row;
}

.styles_heading {
    text-transform: capitalize;
    font-size: 36px;
    margin-inline-start: 16px;
}

.add_form {
    display: flex;
    justify-content: space-between;
}

.input_container {
    display: flex;
    flex-direction: column;
}

input {
    padding-block: 12px;
    padding-inline: 48px;
    margin-block: 16px;
    border-radius: 8px;
    border: 1px solid gray;
    background-color: #bae8e8;
}

select {
    padding-block: 12px;
    padding-inline: 48px;
    border-radius: 8px;
    margin-block: 16px;
    background-color: #bae8e8;
}

textarea {
    margin-block: 16px;
    padding-inline: 48px;
    border-radius: 8px;
    background-color: #bae8e8;
}

::placeholder {
    color: black;
    font-size: 16px;
}

.add_button {
    padding-block: 12px;
    padding-inline: 32px;
    border: none;
    border-radius: 8px;
    background-color: orange;
    box-shadow: 2px 3px 3px gray;
}